<template>
	<view class="">
		<navbar :leftIconBool='false' :rightBool='false' Navtitle='新闻列表' @back='back' :navBoolean='false'></navbar>
		<view style="padding: 0 30rpx;" class="">
			<news v-for="(item,index) in newsList" :key='index' :Newsid=item.newsId :imgUrl='item.pictureUrl' :title='item.title' :content='item.synopsis'></news>
			<u-no-network></u-no-network>
		</view>
	</view>
</template>
<script>
	import {getNewsList} from '@/utill/api/news/news.js'
	import news from './components/news.vue'
	export default {
		components:{
			news
		},
		data() {
			return {
				newsList:[],
			}
		},
		methods:{
			back(){
				// #ifdef H5
				uni.showToast({
					title:'无效',
					icon:'none'
				})
				// #endif
				// #ifndef H5
				uni.navigateBack({
					delta:'1'
				})
				// #endif
			},
		},
		onLoad() {
			var timer = setTimeout(function() {
				uni.showLoading({
				  title: '数据加载中...',
				})
			}, 2000)
			// 获取新闻数据
			getNewsList({
			}).then(res=>{
				clearTimeout(timer)
				uni.hideLoading()
				console.log(res)
				if(res.data.code==0){
					this.newsList=res.data.object.list
				}else{
					uni.showToast({
						title:res.data.msg,
						icon:'none'
					})
				}
			}).catch(err=>{
				clearTimeout(timer)
				uni.hideLoading()
				console.log(err)
			})
		},
	}
</script>

